@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200;300;400;600&family=Roboto:wght@300;400;700&display=swap);
aside {
  width: 80px;
  padding: 20px 0;
  background-color: #fff;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
}
nav {
  width: 100%;
  height: 100%;
}
nav .logo {
  width: 80px;
  height: 50px;
  align-self: flex-start;
  font-size: 56px;
  color: #5965e0;
}
nav ul {
  width: 80px;
  flex: 1 1;
}
nav ul li {
  width: 80px;
  height: 50px;
  cursor: pointer;
  font-size: 20px;
  color: #666666;
  display: flex;
  align-items: center;
  justify-content: center;
}
nav ul li:hover {
  background-color: #f8f8f8;
  border-right: 4px solid #5965e0;
}



button {
  outline: 0;
  border: none;
  background: transparent;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.btn {
  display: inline-block;
  margin: 2px;

  border-radius: 2px;
  text-align: center;
  font-family: "Roboto Mono", monospace;
  font-weight: 600;
  transition: all 0.2s ease-out;
}
.btn-large {
  width: 100%;
  height: 35px;
  line-height: 35px;
  padding: 0 20px;
  border-radius: 3px;
  font-size: 14px;
}
.btn-default {
  color: #fff;
  background: #5965e0;
}
.btn-default:hover {
  background: #fff;
  color: #5965e0;
  border: 1px solid #5965e0;
}
.btn-red {
  color: #fff;
  background: #f00;
}
.btn-red:hover {
  background: #fff;
  color: #f00;
  border: 1px solid #f00;
}

input {
  width: 100%;
  height: 35px;
  padding: 0px 15px;
  border-radius: 3px 0 0 3px;
  border: 1px solid #dde4e6;
  background: #fff;
  font-size: 12px;
  line-height: 12px;
  font-family: "Roboto Mono", sans-serif;
}
label {
  display: inline-block;
  font-size: 14px;
  font-weight: 600;
  color: #666666;
  margin-bottom: 10px;
}
.error-message {
  margin: 0 0 10px !important;
  color: #f00 !important;
  font-size: 10px !important;
}

.content {
  width: 100%;
  max-width: 500px;
  height: 100vh;
  margin: 0 auto;
}
.content .login {
  background-color: #fff;
  border-radius: 3px;
  padding: 60px 10px;
}
.content h1 {
  font-size: 28px;
  font-weight: 600;
  text-transform: uppercase;
}
.content p {
  color: #666666;
}
.content .logo {
  font-size: 56px;
  color: #5965e0;
}
.content p {
  display: inline-block;
  width: 100%;
  font-size: 12px;
  color: #a3a2a1;
  font-weight: 600;
  margin-top: 10px;
  margin-bottom: 10px;
}
.text-align-right {
  text-align: right;
}
.text-align-center {
  text-align: center;
}
.has-account {
  color: #28f5be;
  cursor: pointer;
}
.has-account:hover {
  color: #000000;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* RESET */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  vertical-align: baseline;
}
html,
body {
  color: #2e384d;
  background-color: #e5e5e5;
}
body {
  font-family: "Roboto", sans-serif;
  overflow-x: hidden;
}
ol,
ul {
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
}
span {
  display: inline-block;
}
a {
  text-decoration: none;
  color: inherit;
}
a,
p {
  font-family: "Roboto Mono", sans-serif;
  font-size: 14px;
}
/* SCROLLBAR */
::-webkit-scrollbar {
  width: 7px !important;
}
::-webkit-scrollbar-track {
  background: #232323 !important;
}
::-webkit-scrollbar-thumb {
  background: #232323 !important;
}
::-webkit-scrollbar-thumb:hover {
  background: #28f5be !important;
}
/* CLASSES GERAIS */
.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
@media (min-width: 1920px) {
}
.display-flex {
  display: flex;
}
.align-items-center {
  align-items: center;
}
.justify-content-center {
  justify-content: center;
}
.flex-direction-row {
  flex-direction: row;
}
.flex-direction-column {
  flex-direction: column;
}

